key=\"index\">{{item}}</span>\n              </p>\n            </div>\n          </div>\n          <div class=\"item-price-right\">\n            <div class=\"item-remarks-sum\">\n              <p>累计评价</p>\n              <p>\n                <span class=\"item-remarks-num\">{{goodsInfo.remarksNum}} 条</span>\n              </p>\n            </div>\n          </div>\n        </div>\n        <!-- 选择颜色 -->\n        <div class=\"item-select\">\n          <div class=\"item-select-title\">\n            <p>选择颜色</p>\n          </div>\n          <div class=\"item-select-column\">\n            <div class=\"item-select-row\" v-for=\"(items, index) in goodsInfo.setMeal\" :key=\"index\">\n              <div class=\"item-select-box\" v-for=\"(item, index1) in items\" :key=\"index1\" @click=\"select(index, index1)\" :class=\"{'item-select-box-active': ((index * 3) + index1) === selectBoxIndex}\">\n                <div class=\"item-select-img\">\n                  <img :src=\"item.img\" alt=\"\">\n                </div>\n                <div class=\"item-select-intro\">\n                  <p>{{item.intro}}</p>\n                </div>\n              </div>\n            </div>\n          </div>\n        </div>\n        <!-- 白条分期 -->\n        <div class=\"item-select\">\n          <div class=\"item-select-title\">\n            <p>白条分期</p>\n          </div>\n          <div class=\"item-select-row\">\n            <div class=\"item-select-class\" v-for=\"(item,index) in hirePurchase\" :key=\"index\">\n              <Tooltip :content=\"item.tooltip\" placement=\"top-start\">\n                <span>{{item.type}}</span>\n              </Tooltip>\n            </div>\n          </div>\n        </div>\n        <br>\n        <div class=\"add-buy-car-box\">\n          <div class=\"add-buy-car\">\n            <InputNumber :min=\"1\" v-model=\"count\" size=\"large\"></InputNumber>\n            <Button type=\"error\" size=\"large\" @click=\"addShoppingCartBtn()\">加入购物车</Button>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport store from '@/vuex/store';\nimport { mapState, mapActions } from 'vuex';\nexport default {\n  name: 'ShowGoods',\n  data () {\n    return {\n      price: 0,\n      count: 1,\n      selectBoxIndex: 0,\n      imgIndex: 0\n    };\n  },\n  computed: {\n    ...mapState(['goodsInfo']),\n    hirePurchase () {\n      const three = this.price * this.count / 3;\n      const sex = this.price * this.count / 6;\n      const twelve = this.price * this.count / 12 * 1.0025;\n      const twentyFour = this.price * this.count / 24 * 1.005;\n      return [\n        {\n          tooltip: '无手续费',\n          type: '不分期'\n        },\n        {\n          tooltip: '无手续费',\n          type: `￥${three.toFixed(2)} x 3期`\n        },\n        {\n          tooltip: '无手续费',\n          type: `￥${sex.toFixed(2)} x 6期`\n        },\n        {\n          tooltip: '含手续费：费率0.25%起，￥0.1起×12期',\n          type: `￥${twelve.toFixed(2)} x 12期`\n        },\n        {\n          tooltip: '含手续费：费率0.5%起，￥0.1起×12期',\n          type: `￥${twentyFour.toFixed(2)} x 24期`\n        }\n      ];\n    }\n  },\n  methods: {\n    ...mapActions(['addShoppingCart']),\n    select (index1, index2) {\n      this.selectBoxIndex = index1 * 3 + index2;\n      this.price = this.goodsInfo.setMeal[index1][index2].price;\n    },\n    showBigImg (index) {\n      this.imgIndex = index;\n    },\n    addShoppingCartBtn () {\n      const index1 = parseInt(this.selectBoxIndex / 3);\n      const index2 = this.selectBoxIndex % 3;\n      const date = new Date();\n      const goodsId = date.getTime();\n      const data = {\n        goods_id: goodsId,\n        title: this.goodsInfo.title,\n        count: this.count,\n        package: this.goodsInfo.setMeal[index1][index2]\n      };\n      this.addShoppingCart(data);\n      this.$router.push('/shoppingCart');\n    }\n  },\n  mounted () {\n    const father = this;\n    setTimeout(() => {\n      father.price = father.goodsInfo.setMeal[0][0].price || 0;\n    }, 300);\n  },\n  store\n};\n</script>\n\n<style scoped>\n/******************商品图片及购买详情开始******************/\n.item-detail-show {\n  width: 80%;\n  margin: 15px auto;\n  display: flex;\n  flex-direction: row;\n  background-color: #fff;\n}\n.item-detail-left {\n  width: 350px;\n  margin-right: 30px;\n}\n.item-detail-big-img {\n  width: 350px;\n  height: 350px;\n  box-shadow: 0px 0px 8px #ccc;\n  cursor: pointer;\n}\n.item-detail-big-img img {\n  width: 100%;\n}\n.item-detail-img-row {\n  margin-top: 15px;\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n}\n.item-detail-img-small {\n  width: 68px;\n  height: 68px;\n  box-shadow: 0px 0px 8px #ccc;\n  cursor: pointer;\n}\n.item-detail-img-small img {\n  width: 100%;\n}\n/*商品选购详情*/\n.item-detail-right {\n  display: flex;\n  flex-direction: column;\n}\n.item-detail-title p {\n  color: #666;\n  font-size: 20px;\n}\n.item-detail-express {\n  font-size: 14px;\n  padding: 2px 3px;\n  border-radius: 3px;\n  background-color: #e4393c;\n  color: #fff;\n}\n/*商品标签*/\n.item-detail-tag {\n  font-size: 12px;\n  color: #e4393c;\n}\n/*价格详情等*/\n.item-detail-price-row {\n  padding: 5px;\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n  background-color: #f3f3f3;\n}\n.item-price-left {\n  display: flex;\n  flex-direction: column;\n}\n.item-price-title {\n  color: #999999;\n  font-size: 14px;\n  margin-right: 15px;\n}\n.item-price-row {\n  margin: 5px 0px;\n}\n.item-price {\n  color: #e4393c;\n  font-size: 23px;\n  cursor: pointer;\n}\n.item-price-full-cut {\n  margin-right: 5px;\n  padding: 3px;\n  color: #e4393c;\n  font-size: 12px;\n  background-color: #ffdedf;\n  border: 1px dotted #e4393c;\n  cursor: pointer;\n}\n.item-remarks-sum {\n  padding-left: 8px;\n  border-left: 1px solid #ccc;\n}\n.item-remarks-sum p {\n  color: #999999;\n  font-size: 12px;\n  line-height: 10px;\n  text-align: center;\n}\n.item-remarks-num {\n  line-height: 18px;\n  color: #005eb7;\n}\n.item-select {\n  display: flex;\n  flex-direction: row;\n  margin-top: 15px;\n}\n.item-select-title {\n  color: #999999;\n  font-size: 14px;\n  margin-right: 15px;\n}\n.item-select-column {\n  display: flex;\n  flex-direction: column;\n}\n.item-select-row {\n  display: flex;\n  flex-direction: row;\n  margin-bottom: 8px;\n}\n.item-select-box {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n}\n.item-select-img {\n  width: 36px;\n}\n.item-select-box {\n  padding: 5px;\n  margin-right: 8px;\n  background-color: #f7f7f7;\n  border: 0.5px solid #ccc;\n  cursor: pointer;\n}\n.item-select-box:hover {\n  border: 0.5px solid #e3393c;\n}\n.item-select-box-active {\n  border: 0.5px solid #e3393c;\n}\n.item-select-img img {\n  width: 100%;\n}\n.item-select-intro p {\n  margin: 0px;\n  padding: 5px;\n}\n.item-select-class {\n  padding: 5px;\n  margin-right: 8px;\n  background-color: #f7f7f7;\n  border: 0.5px solid #ccc;\n  cursor: pointer;\n}\n.item-select-class:hover {\n  border: 0.5px solid #e3393c;\n}\n.add-buy-car-box {\n  width: 100%;\n  margin-top: 15px;\n  border-top: 1px dotted #ccc;\n}\n.add-buy-car {\n  margin-top: 15px;\n}\n/******************商品图片及购买详情结束******************/\n</style>\n"],"sourceRoot":""}]);

